.searchForm {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.searchFormInput {
  padding: 8px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 8px;
}

.searchFormButton {
  padding: 8px 16px;
  font-size: 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.searchFormInput:focus {
  border-color: #007bff;
  outline: none;
}
